/*instagram */
.juicer-feed h1.referral {
  margin: 0 !important;
}
.juicer-feed h1.referral a:after {
  display: none !important;
}
.j-text,
.j-poster,
.j-paginate {
  display: none !important;
}
.j-stacker > div {
  padding: 0 !important;
}

.inspiry-blogs .social-section {
  text-transform: uppercase;
  letter-spacing: 1px;
  background: white;
  max-width: 1300px;
  position: relative;
}
.inspiry-blogs .social-section .overlay {
  display: block;
  position: absolute;
}

.feed-item {
  transition: all 0.3s ease;
  width: 250px !important;
  margin: 0 !important;
}
.social-section .j-stacker {
  display: flex !important;
  justify-content: center !important;
  flex-direction: row !important;
}
.j-stack {
  width: 250px !important;
  margin: 5px;
}
.j-stack li {
  margin: 10px !important;
}
.j-image img:hover {
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}

/*single blog*/
.single-blog {
  max-width: 1200px;
  margin: 0 auto;
}
.single-blog p {
  font-family: "Work Sans", sans-serif;
  line-height: 1.5em;
}
